@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400&display=swap);

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 100%;
}

.img-full {
  width: 100%;
}

.container-md {
  max-width: 1400px;
  margin: 0 auto;
}

.card-container {
  padding: 25px 0;
}

.navbar-brand {
  max-width: 390px;
}

.navbar {
  padding: 1.75rem 1rem;
}

@media (max-width: 600px) {
  .navbar {
    padding: 1.25rem 0;
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar-brand {
    max-width: 100%;
    margin: 0 auto;
  }
  button.navbar-toggler {
    margin-top: 25px;
  }
}

#hero-img {
  max-width: 1200px;
  align-self: center;
}

#hero-img-container {
  justify-content: center;
  padding-bottom: 25px;
}

@media (min-width: 601px) {
  .mobile-photos {
    display: none;
  }
}

#orders {
  padding-bottom: 40px;
}

#orders p {
  font-family: 'Montserrat', sans-serif;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}

#orders-now {
  letter-spacing: 6px;
  font-size: 2em;
  color: #535484;
}

#orders-limited {
  letter-spacing: 5px;
  font-size: 1.5m;
  color: #535484;
}

@media (max-width: 600px) {
  #orders-now {
    padding-top: 60px;
    letter-spacing: 5px;
    font-size: 1.1em;
  }

  #orders-limited {
    letter-spacing: 4px;
    font-size: 0.8em;
  }
}

.contact-img-row {
  text-align: center;
  padding: 45px 0;
}

.contact-img-row img {
  max-width: 1100px;
  width: 100%;
}

.contact-container {
  padding: 45px 0 25px 0;
  text-align: center;
}

#ReactGridGallery {
  text-align: center;
}

.ReactGridGallery_tile {
  float: none !important;
  display: inline-block;
  margin: 0 3px !important;
}

@media (max-width: 580px) {
  .ReactGridGallery_tile-viewport {
    height: auto !important;
  }
  .ReactGridGallery_tile-viewport img {
    width: 100%;
    height: auto !important;
  }

  .ReactGridGallery_tile {
    display: block;
  }
}

.req {
  color: red;
  padding-left: 2px;
}

.error {
  border: solid 2px red;
}

#formContainer {
  max-width: 600px;
  margin: 0 auto;
}
#formHeading {
  font-size: 2em;
  font-weight: 100;
  letter-spacing: 1px;
  padding-bottom: 15px;
}
.grayRow {
  background: #f5f5f5;
  padding: 15px 0;
  margin-bottom: 40px;
}

#colorName {
  font-size: 2.6em;
  letter-spacing: 4px;
  color: #4d4d4d;
  text-align: center;
  padding-bottom: 75px;
  margin-top: -40px;
}

.colorRow img:hover {
  cursor: pointer;
  opacity: 0.5;
}

.colorRow {
  padding-bottom: 65px;
}

#colorImage {
  margin-top: -20px;
}

.colorRow .col {
  min-width: 12%;
}

@media (max-width: 1150px) {
  #colorName {
    font-size: 2em;
    letter-spacing: 3px;
    padding-bottom: 50px;
    margin-top: 0;
  }

  .colorRow .col {
    min-width: 25%;
    max-width: 50%;
  }
}

@media (max-width: 500px) {
  .colorRow .col {
    min-width: 50%;
  }
}

footer {
  margin-top: auto;
  padding: 15px 0;
  font-size: 0.7em;
  color: #8a8a8a;
  letter-spacing: 0.1em;
}

